import ms from 'ms';

import JWT from 'jsonwebtoken';
import env from '../../../config/env';

export default function generateJwtToken(userId, issueTime) {
  const payload = { userId, issueTime };
  const secret = env.JWT_REFRESH_SECRET;
  const tokenExpiry = {
    expiresIn: ms(env.TOKEN_EXPIRY),
  };
  const jwt = JWT.sign(payload, secret, tokenExpiry);

  return jwt;
}
